import {TranslateService} from "@ngx-translate/core";

interface EnabledTools{
  contrast: string;
  links: string;
  biggerText: string;
  textSpacing: string;
  imagesRemove: string;
  lineHeight: string;
  saturation: string;
  mouseCursor: string;
  siteStructure: string;
  tooltips: string;
  readableText: string;
  stopAnimation: string;
}

